import React from 'react';
import { Navbar, Nav } from 'react-bootstrap';
import './nav.css';
import logo from '../images/VNTGE.png'

function NavBar() {
  return (
    <div className="box">
      <Navbar collapseOnSelect expand="lg" variant="dark" className="bc">
  <Navbar.Brand href="#home" className="logo">
    <img src={logo} alt="logo" />
  </Navbar.Brand>
  <Navbar.Toggle aria-controls="responsive-navbar-nav" className="toggle"/>
  <Navbar.Collapse id="responsive-navbar-nav">
    <Nav className="m-auto">
     
    </Nav>
    <Nav className="navitems">
      <Nav.Link href="#deets" className="white pr-5">ALL CARS</Nav.Link>
      <Nav.Link eventKey={2} href="#memes" className="white pr-5">
        NEW ARRIVALS
      </Nav.Link>
      <Nav.Link href="#deets" className="white pr-5">AUCTION</Nav.Link>
    </Nav>
  </Navbar.Collapse>
</Navbar>
    </div>
  )
}

export default NavBar;